</template>

<script>
  import header from '@/components/header/header'
  import box from '@/components/box/box'
  import content from '@/components/content/content'
  import menu from '@/components/menu/menu'
  export default {
    name: 'home',
    components: {
      'v-header': header,
      'v-box': box,
      'v-content': content,
      'v-menu': menu
    },
    data () {
      return {
        headerInfo: {}
      }
    }
  }
</script>

<style>
  *{
    padding: 0;
    margin: 0;
  }
</style>



